            <p>Nova prisustva</p>
          </button>

          <Period
            @dates="startPeriod"
            @filter-period="filterItems"
            :isGrid="true"
          />
        </div>

        <search-bar
          :searchStr="search"
          :pageSizeNr="pageSize"
          :searchType="'traži (dan, mjesec, godina)'"
          @changed="setPageSize"
          @typed="searchItems"
        />

        <div class="">
          <div class="days__list attend__heading">
            <span>Datum</span>
            <span>Prisustvovale</span>
            <span>Napomena</span>
          </div>
          <div
            v-for="attendance in pageOfItems"
            :key="attendance._id"
            @click="selectAttendance(attendance)"
            class="clients__list attend__list"
          >
            <p class="client__item">
              {{ attendance.attend_date | formatDate }}
            </p>
            <p class="client__item attend__check">
              {{ mapAttendances(attendance) }}
            </p>
            <p class="client__item">{{ attendance.notes }}</p>
          </div>
        </div>
      </div>
    </div>
    <!-- </transition> -->

    <jw-pagination
      :items="filteredClients"
      @changePage="onChangePage"
      :initialPage="initialPage"
      :pageSize="pageSize"
      :labels="customLabels"
      :styles="customStyles"
      class="pagine"
    >
    </jw-pagination>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import Loading from "@/components/utils/Loading.vue";
import { customLabels, customStyles } from "@/components/utils/pageNav.js";
import SearchBar from "@/components/utils/SearchBar.vue";
import Period from "@/components/utils/Period.vue";
import navigation from "@/mixins/navigation";
import navigationSearch from "@/mixins/navigationSearch";
import searchClients from "@/mixins/searchClients";
import dayjs from "dayjs";
import sr from "dayjs/locale/sr";

dayjs.locale(sr);

export default {
  name: "Attendances",

  components: {
    Loading,
    SearchBar,
    Period,
  },

  mixins: [navigation, navigationSearch, searchClients],

  data() {
    return {
      customLabels,
      customStyles,
    };
  },

  computed: {
    ...mapGetters([
      "getAllAttendances",
      "getClientsPage",
      "getClientsPageSize",
      "loadingState",
    ]),
  },

  methods: {
    ...mapActions([
      "fetchAttendances",
      "fetchAttendance",
      "fetchClientsPage",
      "fetchClientsPageSize",
      "attendanceClear",
      "setLoadingState",
    ]),

    async newAttendance() {
      this.setLoadingState(true);
      this.$router.push("/attendance");
      await this.attendanceClear();
    },

    async selectAttendance(attendance) {
      this.setLoadingState(true);
      await this.fetchAttendance(attendance);
      this.setPageNr();
      this.$router.push("/attendance");
    },

    mapAttendances(attendance) {
      return attendance.members.reduce((a, { present }) => a + present, 0);
    },

    async searchItems(val = "") {
      await this.initItems();
      let mu = this.getAllAttendances.filter((post) => {
        return (
          this.$options.filters
            .formatDate(post.attend_date)
            .toLowerCase()
            .includes(val.toLowerCase()) ||
          post.notes.includes(val.toLowerCase())
        );
      });
      this.filteredClients = mu;
    },

    startPeriod(dateFrom, dateTill) {
      this.filterItems(dateFrom, dateTill);
    },

    filterItems(dateFrom, dateTill) {
      this.dateFrom = dateFrom;
      this.dateTill = dateTill;
      let mu = this.getAllAttendances.filter(
        (year) =>
          dayjs(year.attend_date).format("YYYY-MM-DD") >=
            dayjs(dateFrom).format("YYYY-MM-DD") &&
          dayjs(year.attend_date).format("YYYY-MM-DD") <=
            dayjs(dateTill).format("YYYY-MM-DD")
      );
      this.filteredClients = mu;
    },

    async initItems() {
      if (!this.getAllAttendances.length) await this.fetchAttendances();
      this.filteredClients = this.getAllAttendances;
      this.startPeriod(this.dateFrom, this.dateTill);
      if (this.getClientsPage !== 1) this.initialPage = this.getClientsPage;
      if (this.getClientsPageSize !== 10)
        this.pageSize = this.getClientsPageSize;
    },
  },

  filters: {
    formatDate: function (value) {
      if (value) {
        return dayjs(String(value)).format("dddd, DD. MMM YYYY");
      }
    },
  },

  async mounted() {
    await this.initItems();
    this.setLoadingState(false);
  },
};
</script>

<style>
.attend__check {
  text-align: center;
}
</style>
